// src/contexts/GameSaveContext.jsx
import React, { createContext, useContext, useState, useEffect, useRef, useCallback } from 'react';
import { 
  doc, 
  collection, 
  setDoc, 
  getDoc, 
  getDocs, 
  Timestamp,
  writeBatch 
} from 'firebase/firestore';
import { db } from '../firebaseConfig';
import { useAuth } from './AuthContext';
import { SERVER_URL, isTauri } from '../utils/api';
const API_URL = SERVER_URL;

// GameSave-Context erstellen
const GameSaveContext = createContext({});


// Hook zum einfachen Zugriff auf den GameSave-Context
export const useGameSave = () => useContext(GameSaveContext);

export const GameSaveProvider = ({ children }) => {
  const { user, isAuthenticated } = useAuth();
  const [syncStatus, setSyncStatus] = useState('idle'); // idle, syncing, synced, error
  const [syncQueue, setSyncQueue] = useState({});
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const [lastSync, setLastSync] = useState(null);
  const [syncTimeoutId, setSyncTimeoutId] = useState(null);
  const syncQueueRef = useRef(syncQueue);
  const [syncErrorCount, setSyncErrorCount] = useState(0); // Zhler fr Fehler
  const [syncErrorItems, setSyncErrorItems] = useState({}); // Speichert Elemente, die Fehler verursachen

  // Debug-Informationen fr Firebase User
  useEffect(() => {
    if (user) {
      console.log("Auth Debug - User Objekt:", {
        _id: user._id,
        uid: user.uid,
        userId: user._id || user.uid // Wir verwenden diese Variable spter
      });
    }
  }, [user]);

  // Hilfsfunktion zur Bereinigung von Daten fr Firestore
  const sanitizeDataForFirestore = (data) => {
    try {
      // Versuche das Objekt zu JSON zu serialisieren und wieder zurck
      // Dies entfernt kreisfrmige Referenzen und Funktionen
      return JSON.parse(JSON.stringify(data));
    } catch (err) {
      console.error("Fehler beim Serialisieren von Daten fr Firestore:", err);
      // Im Fehlerfall gib ein leeres Objekt zurck
      return {};
    }
  };

  // Hilfsfunktion zum Erstellen eines benutzerspezifischen localStorage-Schlssels
  const getUserStorageKey = (key) => {
    const userId = user?.uid || user?._id;
    return userId ? `user_${userId}_${key}` : `anonymous_${key}`;
  };

  // Hilfsfunktion zum Lschen aller lokalen Spielstnde eines Benutzers
  const clearUserLocalStorage = (userId) => {
    if (!userId) return;
    
    // Alle localStorage-Schlssel durchgehen
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key.startsWith(`user_${userId}_`)) {
        localStorage.removeItem(key);
      }
    }
  };

  // Aktualisiere die Referenz, wenn sich syncQueue ndert
  useEffect(() => {
    syncQueueRef.current = syncQueue;
  }, [syncQueue]);

  // Online-Status berwachen
  useEffect(() => {
    const handleOnline = () => {
      setIsOnline(true);
      // Versuche zu synchronisieren, wenn wieder online
      const currentQueue = syncQueueRef.current;
      if (Object.keys(currentQueue).length > 0) {
        batchSyncToFirebase();
      }
    };
    const handleOffline = () => setIsOnline(false);
    
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
    
    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // Wenn Benutzer-ID sich ndert, setze Synchronisationsstatus zurck
  useEffect(() => {
    if (isAuthenticated && user) {
      const userId = user.uid || user._id; // Beide Varianten untersttzen
      console.log("Benutzer authentifiziert:", userId);
      setSyncStatus('idle');
      
      // Initialen Sync durchfhren
      syncFromFirebase();
    } else {
      setSyncStatus('idle');
      setSyncQueue({});
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isAuthenticated, user?.uid, user?._id]);

  // Debounced Batch-Synchronisation mit Firebase
  const batchSyncToFirebase = useCallback(async () => {
    if (!isAuthenticated || !user || !isOnline || Object.keys(syncQueueRef.current).length === 0) return;

    // Die korrekte User-ID ermitteln
    const userId = user.uid || user._id;
    if (!userId) {
      console.error("Keine Benutzer-ID gefunden fr die Synchronisation");
      return;
    }

    // Bereits laufenden Timeout abbrechen
    if (syncTimeoutId) {
      clearTimeout(syncTimeoutId);
    }

    // Neuen Timeout setzen
    const timeoutId = setTimeout(async () => {
      try {
        setSyncStatus('syncing');
        console.log("Starte Batch-Synchronisation mit Firebase...");

        const batch = writeBatch(db);
        const currentQueue = { ...syncQueueRef.current };

        // Fr jeden Eintrag in der Warteschlange
        for (const [key, data] of Object.entries(currentQueue)) {
          // berspringe Elemente, die bereits Fehler verursacht haben
          if (syncErrorItems[key]) {
            console.warn(`berspringe "${key}" wegen vorheriger Fehler`);
            continue;
          }

          const docRef = doc(db, 'users', userId, 'gameData', key);
          const sanitizedData = sanitizeDataForFirestore(data);
          
          batch.set(docRef, {
            data: sanitizedData,
            updatedAt: Timestamp.now()
          });
        }

        await batch.commit();
        console.log("Batch-Synchronisation erfolgreich!");
        
        // Warteschlange leeren, nachdem alle Daten synchronisiert wurden
        setSyncQueue({});
        setSyncErrorCount(0); // Fehler zurcksetzen
        setSyncStatus('synced');
        setLastSync(new Date().toISOString());
      } catch (error) {
        console.error("Fehler bei der Batch-Synchronisation:", error);
        setSyncStatus('error');
        setSyncErrorCount(prev => prev + 1);
        
        // Bei zu vielen Fehlern die Warteschlange leeren
        if (syncErrorCount > 3) {
          console.warn("Zu viele Fehler bei der Synchronisation. Warteschlange wird geleert.");
          setSyncQueue({});
          setSyncErrorItems({});
          setSyncErrorCount(0);
        }
      }
    }, 1000); // 1 Sekunde Verzgerung

    setSyncTimeoutId(timeoutId);
  }, [isAuthenticated, isOnline, syncTimeoutId, user, syncErrorItems, syncErrorCount]);

  // Spielstand speichern - MIT BENUTZERSPEZIFISCHEM SCHLSSEL
  const saveGameState = useCallback(async (key, data) => {
    if (!key || data === undefined) {
      console.error("Ungltige Parameter fr saveGameState:", { key, data });
      return false;
    }

    try {
      // Benutzer-spezifischen Schlssel erstellen
      const storageKey = getUserStorageKey(key);
      
      // Mit dem benutzerabhngigen Schlssel speichern
      localStorage.setItem(storageKey, JSON.stringify(data));
      console.log(`Spielstand "${key}" lokal gespeichert.`);
      
      // Wenn authentifiziert, zur Sync-Warteschlange hinzufgen
      if (isAuthenticated && user) {
        // Die korrekte User-ID ermitteln
        const userId = user.uid || user._id;
        if (!userId) {
          console.error("Keine Benutzer-ID gefunden fr die Synchronisation");
          return true; // Trotzdem lokalen Speichererfolg zurckgeben
        }

        // berprfen, ob dieses Element bereits fehlgeschlagen ist
        if (syncErrorItems[key]) {
          console.warn(`berspringe "${key}" wegen vorheriger Synchronisierungsfehler`);
          return true; // Lokaler Speichererfolg
        }

        setSyncQueue(prev => ({
          ...prev,
          [key]: data
        }));
        
        // Wenn online, sofort synchronisieren
        if (isOnline) {
          console.log(`Spielstand "${key}" wird mit Firebase synchronisiert.`);
          
          try {
            const docRef = doc(db, 'users', userId, 'gameData', key);
            const sanitizedData = sanitizeDataForFirestore(data);
            
            await setDoc(docRef, {
              data: sanitizedData,
              updatedAt: Timestamp.now()
            });
            
            // Entferne den erfolgreichen Eintrag aus der Warteschlange
            setSyncQueue(prev => {
              const { [key]: removed, ...rest } = prev;
              return rest;
            });
            
            // Entferne aus der Fehlerliste, falls es dort war
            if (syncErrorItems[key]) {
              setSyncErrorItems(prev => {
                const { [key]: removed, ...rest } = prev;
                return rest;
              });
            }
            
            setSyncStatus('synced');
            setLastSync(new Date().toISOString());
            console.log(`Spielstand "${key}" erfolgreich synchronisiert.`);
          } catch (error) {
            console.error(`Fehler beim Synchronisieren von "${key}":`, error);
            setSyncStatus('error');

            // Berechtigungsfehler behandeln
            if (error && (
              error.message.includes("Missing or insufficient permissions") || 
              error.code === 'permission-denied' || 
              error.code === 'unauthenticated')) {
              
              console.warn(`Berechtigungsfehler fr "${key}": Entferne aus der Warteschlange`);
              
              // Entferne den Eintrag aus der Warteschlange
              setSyncQueue(prev => {
                const { [key]: removed, ...rest } = prev;
                return rest;
              });
              
              // Fge zur Fehlerliste hinzu
              setSyncErrorItems(prev => ({
                ...prev,
                [key]: {
                  error: error.message,
                  timestamp: new Date().toISOString()
                }
              }));
            }
            
            return false;
          }
        } else {
          console.log(`Spielstand "${key}" zur Offline-Warteschlange hinzugefgt.`);
        }
      }
      
      return true;
    } catch (error) {
      console.error(`Fehler beim Speichern von "${key}":`, error);
      return false;
    }
  }, [isAuthenticated, isOnline, user, syncErrorItems]);

  // Spielstand laden - MIT BENUTZERSPEZIFISCHEM SCHLSSEL UND TYPBERPRFUNG
const loadGameState = useCallback(async (key) => {
  if (!key) {
    console.error("Kein Schlssel fr loadGameState angegeben");
    return null;
  }

  try {
    // Standardwerte fr verschiedene Datentypen definieren
    const defaultValues = {
      'dailyWords': [],
      'wordCategories': [],
      'sentenceCategories': [],
      'dailySentences': [],
      'difficultWords': {},
      'missionsFortschritt': {
        erstellt: 0,
        gelernt: 0,
        verben: 0,
        gesamtPunkte: 0
      }
    };

    // Zuerst versuchen, Daten aus Firebase zu laden (wenn authentifiziert und online)
    if (isAuthenticated && user && isOnline) {
      try {
        // Die korrekte User-ID ermitteln
        const userId = user.uid || user._id;
        if (!userId) {
          console.error("Keine Benutzer-ID gefunden fr das Laden");
          // Fallback auf localStorage
        } else {
          const docRef = doc(db, 'users', userId, 'gameData', key);
          const docSnap = await getDoc(docRef);
          
          if (docSnap.exists()) {
            const remoteData = docSnap.data().data;
            console.log(`Spielstand "${key}" aus Firebase geladen.`);
            
            // Prfen, ob die geladenen Daten den erwarteten Typ haben
            if (Array.isArray(defaultValues[key]) && !Array.isArray(remoteData)) {
              console.warn(`Typ-Korrektur fr ${key}: Objekt -> Array`);
              const correctedData = Array.isArray(remoteData) ? remoteData : [];
              
              // In localStorage speichern mit benutzerspezifischem Schlssel
              const storageKey = getUserStorageKey(key);
              localStorage.setItem(storageKey, JSON.stringify(correctedData));
              
              return correctedData;
            } 
            else if (typeof defaultValues[key] === 'object' && !Array.isArray(defaultValues[key]) && 
                    (Array.isArray(remoteData) || typeof remoteData !== 'object')) {
              console.warn(`Typ-Korrektur fr ${key}: ${typeof remoteData} -> Objekt`);
              const correctedData = typeof remoteData === 'object' && !Array.isArray(remoteData) ? 
                                    remoteData : defaultValues[key];
              
              // In localStorage speichern mit benutzerspezifischem Schlssel
              const storageKey = getUserStorageKey(key);
              localStorage.setItem(storageKey, JSON.stringify(correctedData));
              
              return correctedData;
            }
            
            // In localStorage speichern mit benutzerspezifischem Schlssel
            const storageKey = getUserStorageKey(key);
            localStorage.setItem(storageKey, JSON.stringify(remoteData));
            
            return remoteData;
          }
        }
      } catch (error) {
        console.warn(`Fehler beim Laden von "${key}" aus Firebase:`, error);
        // Fallback auf localStorage
      }
    }
    
    // Daten aus localStorage laden - MIT BENUTZERSPEZIFISCHEM SCHLSSEL
    const storageKey = getUserStorageKey(key);
    const localData = localStorage.getItem(storageKey);
    if (localData) {
      try {
        const parsedData = JSON.parse(localData);
        
        // Auch hier auf Typkorrektheit prfen
        if (Array.isArray(defaultValues[key]) && !Array.isArray(parsedData)) {
          console.warn(`Typ-Korrektur fr ${key} in localStorage: Objekt -> Array`);
          return [];
        } 
        else if (typeof defaultValues[key] === 'object' && !Array.isArray(defaultValues[key]) && 
                (Array.isArray(parsedData) || typeof parsedData !== 'object')) {
          console.warn(`Typ-Korrektur fr ${key} in localStorage: ${typeof parsedData} -> Objekt`);
          return defaultValues[key];
        }
        
        console.log(`Spielstand "${key}" aus localStorage geladen.`);
        return parsedData;
      } catch (error) {
        console.error(`Fehler beim Parsen von "${key}" aus localStorage:`, error);
        return defaultValues[key] || null;
      }
    }
    
    console.log(`Kein Spielstand fr "${key}" gefunden.`);
    return defaultValues[key] || null;
  } catch (error) {
    console.error(`Fehler beim Laden von "${key}":`, error);
    return null;
  }
}, [isAuthenticated, isOnline, user]);
  // Alle gespeicherten Spielstnde eines Benutzers aus Firebase laden
  const syncFromFirebase = useCallback(async () => {
    if (!isAuthenticated || !user || !isOnline) return;

    // Die korrekte User-ID ermitteln
    const userId = user.uid || user._id;
    if (!userId) {
      console.error("Keine Benutzer-ID gefunden fr die Synchronisation");
      return;
    }

    try {
      setSyncStatus('syncing');
      console.log("Lade alle Spielstnde aus Firebase...");
      
      const gameDataRef = collection(db, 'users', userId, 'gameData');
      const querySnapshot = await getDocs(gameDataRef);
      
      if (querySnapshot.empty) {
        console.log("Keine Spielstnde in Firebase gefunden.");
        setSyncStatus('synced');
        setLastSync(new Date().toISOString());
        return;
      }
      
      // Alle Spielstnde aus Firebase in localStorage speichern
      querySnapshot.forEach((doc) => {
        const key = doc.id;
        const data = doc.data().data;
        
        // Benutzerspezifischen Schlssel fr localStorage erstellen
        const storageKey = getUserStorageKey(key);
        
        // Prfen, ob lokale Daten neuer sind (falls vorhanden)
        const localDataStr = localStorage.getItem(storageKey);
        if (localDataStr) {
          try {
            // Wenn lokale Daten vorhanden, prfen ob sie aktueller sind
            const localData = JSON.parse(localDataStr);
            
            // Einfache Implementierung: Lokale Daten bevorzugen und zur Synchronisation markieren
            if (JSON.stringify(localData).length > JSON.stringify(data).length) {
              console.log(`Lokale Daten fr "${key}" scheinen aktueller zu sein, werden spter synchronisiert.`);
              setSyncQueue(prev => ({
                ...prev,
                [key]: localData
              }));
              return; // berspringe die Ersetzung durch Firebase-Daten
            }
          } catch (error) {
            console.error(`Fehler beim Verarbeiten lokaler Daten fr "${key}":`, error);
          }
        }
        
        // Speichere Firebase-Daten lokal mit benutzerspezifischem Schlssel
        localStorage.setItem(storageKey, JSON.stringify(data));
        console.log(`Spielstand "${key}" aus Firebase in localStorage gespeichert.`);
      });
      
      setSyncStatus('synced');
      setLastSync(new Date().toISOString());
      console.log("Alle Spielstnde erfolgreich synchronisiert.");
      
      // Wenn es Eintrge in der Warteschlange gibt, synchronisiere sie
      if (Object.keys(syncQueueRef.current).length > 0) {
        batchSyncToFirebase();
      }
    } catch (error) {
      console.error("Fehler beim Laden der Spielstnde aus Firebase:", error);
      setSyncStatus('error');
    }
  }, [isAuthenticated, isOnline, user, batchSyncToFirebase]);

  // Manuelle Synchronisation erzwingen
  const forceSync = useCallback(async () => {
    if (!isAuthenticated || !isOnline) {
      return { success: false, message: "Synchronisation nicht mglich: Offline oder nicht angemeldet." };
    }
    
    try {
      // Fehler zurcksetzen
      setSyncErrorCount(0);
      setSyncErrorItems({});
      
      // Zuerst von Firebase laden
      await syncFromFirebase();
      
      // Dann zur Firebase hochladen
      if (Object.keys(syncQueueRef.current).length > 0) {
        await batchSyncToFirebase();
      }
      
      return { success: true, message: "Synchronisation erfolgreich!" };
    } catch (error) {
      console.error("Fehler bei der erzwungenen Synchronisation:", error);
      return { success: false, message: "Synchronisation fehlgeschlagen: " + error.message };
    }
  }, [isAuthenticated, isOnline, syncFromFirebase, batchSyncToFirebase]);

  // Spielstnde exportieren - BENUTZERSPEZIFISCHE SCHLSSEL BERCKSICHTIGEN
  const exportGameData = useCallback(() => {
    try {
      const exportData = {};
      
      // Die bekannten Spielstandschlssel
      const gameStateKeys = [
        'dailyWords', 
        'wordCategories', 
        'sentenceCategories', 
        'dailySentences', 
        'difficultWords', 
        'missionsFortschritt',
        'verbCategories',
        'customVerbs'
      ];
      
      // Fr jeden Schlssel den benutzerspezifischen localStorage-Schlssel generieren
      gameStateKeys.forEach(key => {
        const storageKey = getUserStorageKey(key);
        const value = localStorage.getItem(storageKey);
        
        if (value) {
          try {
            exportData[key] = JSON.parse(value);
          } catch (error) {
            console.error(`Fehler beim Parsen von "${key}":`, error);
            exportData[key] = value; // Rohwert speichern
          }
        }
      });
      
      // Zustzliche Metadaten
      exportData._meta = {
        exportDate: new Date().toISOString(),
        userId: user?.uid || user?._id || 'anonymous',
        version: '1.0'
      };
      
      // Als JSON-String konvertieren
      const jsonString = JSON.stringify(exportData, null, 2);
      
      // JSON-Datei erstellen und herunterladen
      const blob = new Blob([jsonString], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `daz-connect-backup-${new Date().toISOString().slice(0, 10)}.json`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
      
      return { success: true, message: "Spielstnde erfolgreich exportiert!" };
    } catch (error) {
      console.error("Fehler beim Exportieren der Spielstnde:", error);
      return { success: false, message: "Export fehlgeschlagen: " + error.message };
    }
  }, [user]);

  // Spielstnde importieren - MIT BENUTZERSPEZIFISCHEN SCHLSSELN
  const importGameData = useCallback(async (jsonData) => {
    try {
      // JSON-Daten parsen, falls als String bergeben
      const data = typeof jsonData === 'string' ? JSON.parse(jsonData) : jsonData;
      
      // Metadaten validieren
      if (!data._meta || !data._meta.version) {
        return { success: false, message: "Ungltiges Backup-Format: Metadaten fehlen." };
      }
      
      // Die Spielstnde in localStorage speichern
      const gameStateKeys = [
        'dailyWords', 
        'wordCategories', 
        'sentenceCategories', 
        'dailySentences', 
        'difficultWords', 
        'missionsFortschritt',
        'verbCategories',
        'customVerbs'
      ];
      
      gameStateKeys.forEach(key => {
        if (data[key]) {
          // Benutzerspezifischen Schlssel verwenden
          const storageKey = getUserStorageKey(key);
          localStorage.setItem(storageKey, JSON.stringify(data[key]));
          console.log(`Spielstand "${key}" importiert.`);
        }
      });
      
      // Auch in Firebase speichern, wenn online
      if (isAuthenticated && user && isOnline) {
        try {
          // Die korrekte User-ID ermitteln
          const userId = user.uid || user._id;
          if (!userId) {
            console.error("Keine Benutzer-ID gefunden fr den Import");
            // Trotzdem fortfahren mit lokaler Speicherung
          } else {
            const batch = writeBatch(db);
            
            gameStateKeys.forEach(key => {
              if (data[key]) {
                const docRef = doc(db, 'users', userId, 'gameData', key);
                const sanitizedData = sanitizeDataForFirestore(data[key]);
                batch.set(docRef, {
                  data: sanitizedData,
                  updatedAt: Timestamp.now(),
                  importedAt: Timestamp.now()
                });
              }
            });
            
            await batch.commit();
            console.log("Alle importierten Spielstnde erfolgreich mit Firebase synchronisiert.");
          }
        } catch (error) {
          console.error("Fehler beim Synchronisieren der importierten Spielstnde:", error);
          // Weitermachen, auch wenn Firebase-Sync fehlschlgt
        }
      }
      
      return { 
        success: true, 
        message: "Spielstnde erfolgreich importiert! Bitte lade die Seite neu, um die nderungen zu sehen." 
      };
    } catch (error) {
      console.error("Fehler beim Importieren der Spielstnde:", error);
      return { success: false, message: "Import fehlgeschlagen: " + error.message };
    }
  }, [isAuthenticated, isOnline, user]);

  // Spielstnde zurcksetzen - MIT BENUTZERSPEZIFISCHEN SCHLSSELN
  const resetGameData = useCallback(async () => {
    try {
      // Die zu lschenden Schlssel
      const gameStateKeys = [
        'dailyWords', 
        'wordCategories', 
        'sentenceCategories', 
        'dailySentences', 
        'difficultWords', 
        'verbCategories',
        'customVerbs'
      ];
      
      // Die korrekte User-ID ermitteln
      const userId = user?.uid || user?._id;
      
      // Lokale Daten lschen mit benutzerspezifischen Schlsseln
      if (userId) {
        gameStateKeys.forEach(key => {
          const storageKey = getUserStorageKey(key);
          localStorage.removeItem(storageKey);
          console.log(`Spielstand "${key}" zurckgesetzt.`);
        });
      } else {
        console.warn("Keine Benutzer-ID zum Zurcksetzen der Daten gefunden");
      }
      
      // Fortschritt auf 0 setzen, aber nicht komplett lschen
      const defaultProgress = { erstellt: 0, gelernt: 0, verben: 0, gesamtPunkte: 0 };
      const progressKey = getUserStorageKey('missionsFortschritt');
      localStorage.setItem(progressKey, JSON.stringify(defaultProgress));
      
      // Auch in Firebase zurcksetzen, wenn online
      if (isAuthenticated && user && isOnline && userId) {
        try {
          const batch = writeBatch(db);
          
          // Spielstnde lschen (setze auf leere Objekte)
          gameStateKeys.forEach(key => {
            const docRef = doc(db, 'users', userId, 'gameData', key);
            batch.set(docRef, {
              data: {},
              updatedAt: Timestamp.now(),
              resetAt: Timestamp.now()
            });
          });
          
          // Fortschritt zurcksetzen
          const progressRef = doc(db, 'users', userId, 'gameData', 'missionsFortschritt');
          batch.set(progressRef, {
            data: defaultProgress,
            updatedAt: Timestamp.now(),
            resetAt: Timestamp.now()
          });
          
          await batch.commit();
          console.log("Alle Spielstnde erfolgreich in Firebase zurckgesetzt.");
        } catch (error) {
          console.error("Fehler beim Zurcksetzen der Spielstnde in Firebase:", error);
          // Weitermachen, auch wenn Firebase-Reset fehlschlgt
        }
      }
      
      return { 
        success: true, 
        message: "Spielstnde erfolgreich zurckgesetzt! Bitte lade die Seite neu, um die nderungen zu sehen." 
      };
    } catch (error) {
      console.error("Fehler beim Zurcksetzen der Spielstnde:", error);
      return { success: false, message: "Zurcksetzen fehlgeschlagen: " + error.message };
    }
  }, [isAuthenticated, isOnline, user]);

  // Daten beim Logout bereinigen
  const clearUserData = useCallback(() => {
    // Die korrekte User-ID ermitteln
    const userId = user?.uid || user?._id;
    if (userId) {
      // Lokale Daten dieses Benutzers lschen
      clearUserLocalStorage(userId);
      console.log(`Lokale Daten fr Benutzer ${userId} gelscht.`);
    }
    
    // Alle zustandsbezogenen Daten zurcksetzen
    setSyncQueue({});
    setSyncStatus('idle');
    setSyncErrorCount(0);
    setSyncErrorItems({});
    setLastSync(null);
    
    return { success: true, message: "Benutzerdaten erfolgreich gelscht." };
  }, [user]);

  // Warteschlange manuell leeren
  const clearSyncQueue = useCallback(() => {
    setSyncQueue({});
    setSyncErrorItems({});
    setSyncErrorCount(0);
    return { success: true, message: "Synchronisationswarteschlange geleert." };
  }, []);

  // Werte, die im Context zur Verfgung gestellt werden
  const value = {
    saveGameState,
    loadGameState,
    syncStatus,
    isOnline,
    apiUrl: API_URL,
    lastSync,
    forceSync,
    exportGameData,
    importGameData,
    resetGameData,
    syncFromFirebase,
    clearSyncQueue,
    clearUserData, // Neue Funktion zur Bereinigung der Benutzerdaten
    pendingSyncCount: Object.keys(syncQueue).length,
    syncErrorCount   // Fr UI-Feedback
  };

  return (
    <GameSaveContext.Provider value={value}>
      {children}
    </GameSaveContext.Provider>
  );
};

export default GameSaveContext;