import React from 'react';
import styles from './SentenceStructureMenu.module.css';

const SentenceStructureMenu = ({ onSelectLevel }) => {
  return (
    <div className={styles.menu}>
      <p className={styles.title}>
        Whle eine Schwierigkeitsstufe aus:
      </p>
      
      <div className={styles.buttonGrid}>
        <button
          onClick={() => onSelectLevel('easy')}
          className={`${styles.levelButton} ${styles.buttonEasy}`}
        >
          <div className={styles.buttonContent}>
            <div className={styles.buttonText}>
              <span className={styles.heading}>Einfache Stze</span>
              <span className={styles.description}>
                Grundlegende Satzstrukturen mit Subjekt und Prdikat
              </span>
            </div>
            <span className={styles.arrow}></span>
          </div>
        </button>

        <button
          onClick={() => onSelectLevel('medium')}
          className={`${styles.levelButton} ${styles.buttonMedium}`}
        >
          <div className={styles.buttonContent}>
            <div className={styles.buttonText}>
              <span className={styles.heading}>Mittlere Stze</span>
              <span className={styles.description}>
                Erweiterte Stze mit Zeitangaben und Objekten
              </span>
            </div>
            <span className={styles.arrow}></span>
          </div>
        </button>

        <button
          onClick={() => onSelectLevel('hard')}
          className={`${styles.levelButton} ${styles.buttonHard}`}
        >
          <div className={styles.buttonContent}>
            <div className={styles.buttonText}>
              <span className={styles.heading}>Schwere Stze</span>
              <span className={styles.description}>
                Komplexe Stze mit Nebenstzen
              </span>
            </div>
            <span className={styles.arrow}></span>
          </div>
        </button>
      </div>
    </div>
  );
};

export default SentenceStructureMenu;