  // Fllt die fehlenden Level mit generierten Details
  for (let i = LEVEL_DETAILS.length + 1; i <= 100; i++) {
    const baseLevel = LEVEL_DETAILS[Math.min(Math.floor(i/10) * 10 - 1, LEVEL_DETAILS.length - 1)];
    LEVEL_DETAILS.push({
      name: `Level ${i} ${baseLevel.name}`,
      icon: baseLevel.icon + ''.repeat(Math.floor((i % 10) / 3)),
      description: `Fortgeschrittene Stufe: ${baseLevel.description}`
    });
  }
  
  export const calculateLevelInfo = (totalScore) => {
    const currentLevel = Math.min(
      Math.max(Math.floor(totalScore / 100) + 1, 1),
      LEVEL_DETAILS.length
    );
    
    const levelDetails = LEVEL_DETAILS[currentLevel - 1];
    const requiredXP = currentLevel * 100;
    const xpInCurrentLevel = totalScore % 100;
    const progress = currentLevel < 100 ? (xpInCurrentLevel / 100) * 100 : 100;
  
    return {
      currentLevel,
      levelName: levelDetails.name,
      currentXP: totalScore,
      requiredXP: currentLevel < 100 ? requiredXP : totalScore,
      progress: Math.min(progress, 100),
      icon: levelDetails.icon,
      description: levelDetails.description
    };
  };
  
  export const getLevelDetails = (level) => {
    return LEVEL_DETAILS[Math.min(level - 1, LEVEL_DETAILS.length - 1)];
  };